
import { defineComponent } from "vue";
import enumProp from "../helpers/enum-prop";
import {
  baseButtonEvents,
  baseButtonPropSizes,
  baseButtonPropsType,
} from "./BaseButton.types";

export default defineComponent({
  name: "BaseButton",
  props: {
    fullWidth: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      required: true,
    },
    primary: {
      type: Boolean,
      default: false,
    },
    size: {
      ...enumProp("medium", baseButtonPropSizes),
      default: "medium",
    },
    type: {
      ...enumProp("submit", baseButtonPropsType),
      default: "submit",
    },
  },
  emits: [...baseButtonEvents],
  methods: {
    onClick() {
      this.$emit("click");
    },
  },
  computed: {
    classes(): string {
      const c = [
        "base-button",
        `base-button--${this.size}`,
        `base-button--${this.primary ? "primary" : "secondary"}`,
      ];

      if (this.fullWidth) {
        c.push("base-button--full-width");
      }

      return c.join(" ");
    },
  },
});
